* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.title {
    text-align: center;
    height: 25%;
    padding: 13px;
    color: #014871;
}

.container {
    margin: 0 auto;
    width: 300px;
}

.form {
    border-radius: 8px;
    display: flex;
    height: 156px;
    flex-flow: column;
    justify-content: space-around;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
    background-color: whitesmoke;
}

.form button {
    width: 90%;
    height: 23px;
    color: white;
    background-color: #335CC5;
    margin: 8px auto;
    border: none;
    border-radius: 3px;
    transition: all 200ms ease-in-out;
}

.form label {
    margin: 10px 0 0 15px;
}

.form input {
    width: 90%;
    height: 25px;
    margin: 0 auto;
    border: 1px solid lightslategray;
    border-radius: 3px;
}

.form button:hover {
    background-color: #173f5f;
}